@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,500;0,700;0,900;1,300;1,500;1,700&display=swap);
body, p {
  margin: 0;
  font-family: "Segoe UI", "Roboto", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav-item a{color:gray}.menu-collapse{border-color:transparent !important;background-color:transparent !important}.ant-btn-primary{background-color:transparent !important;border-color:transparent !important}.fa-bars{color:gray !important}@media only screen and (max-width: 767px){.nav-item a{color:#000}.fa-bars{color:#000 !important}}
.Banner{height:32em;padding:10px;font-family:sans-serif;background:url(/static/media/teamwork.ba52d243.png);background-size:contain;background-repeat:no-repeat}.Banner .title{height:32em;position:absolute;top:10em;width:35%}.Banner .title p{font-size:14px;color:gray;font-weight:600}.Banner .title h1{font-weight:600;font-size:38px;letter-spacing:1px;color:#036}.Banner .title span{-webkit-align-self:end;align-self:end;color:#036;background-color:#98fb98;display:inline-block;padding:0px;height:.4em;line-height:.2}.Banner .title button{font-size:14px;padding:.3em 2em;box-shadow:2px 5px 5px #aaa;background-color:#1dcce0;border-color:#1dcce0}@media only screen and (max-width: 767px){.Banner .title{width:90%}}@media screen and (min-width: 768px)and (max-width: 991px){.Banner .title{width:35%}}@media screen and (min-width: 992px)and (max-width: 1024px){.Banner .title{width:35%}}@media screen and (min-width: 1024px){.Banner .title{width:24%}}
.Projects{padding:5em 5em;background-color:#001d26}.Projects .card-body{padding-left:0}.Projects .odd-col h4{color:#add8e6}.Projects .odd-col h3{color:#1e90ff}.Projects .odd-col p{color:#add8e6}.Projects .even-col h4{color:#add8e6}.Projects .even-col h3{color:#1e90ff}.Projects .even-col p{color:#add8e6}.Projects .tags{padding-top:1em}.Projects .tags .project-tag{color:#1e90ff;text-transform:uppercase;font-size:12px;padding-right:1em}.Projects .more-prjects{padding-top:2em;text-align:center}.Projects .more-prjects button{color:#fff;border-color:#fff}
.InfoButton{background-color:transparent;border:2px solid indigo;color:indigo;padding:.2em 1em;text-transform:uppercase;font-weight:bold;text-align:center;text-decoration:none;display:inline-block;font-size:14px;margin:4px 2px;border-radius:15px}.InfoButton-padding{background-color:transparent;border:2px solid indigo;color:indigo;padding:.2em 3em;text-transform:uppercase;font-weight:bold;text-align:center;text-decoration:none;display:inline-block;font-size:14px;margin:4px 2px;border-radius:15px}
.Info{padding-bottom:5em}.padding{padding-bottom:2em}.info-bg-1{background:url(/static/media/info_1.3cbf19e0.jpg);object-fit:contain;height:35em}.info-bg-2{background:url(/static/media/info_2.10b316a0.png);object-fit:contain;height:35em}.info{padding:2em 5em;margin:auto}.info h4{font-weight:bold;text-transform:uppercase}.info h3{color:#aaa;letter-spacing:.1;line-height:1}.info p{font-weight:bold;line-height:1.2}.info ul{text-decoration:none;padding-left:0}.info li{font-weight:bold;color:gray;line-height:1.2;padding-bottom:1em;text-transform:uppercase}
.Home{margin:auto}.project-item{margin:0 auto;background-color:#001d26}.project-item p,.project-item h5{color:#fff}.project-itmes:nth-child(1) img,.project-itmes:nth-child(3) img,.project-itmes:nth-child(5) img,.project-itmes:nth-child(7) img{max-width:80%}.project-itmes:nth-child(2) img,.project-itmes:nth-child(4) img,.project-itmes:nth-child(6) img,.project-itmes:nth-child(8) img{max-width:50%}
.ClientsLogo{text-align:center;padding:10em 5em}.ClientsLogo h1{color:#2f4f4f}.ClientsLogo img{padding:.5em}
